.repairContent-1 {
  margin-top: 10vh;
  text-align: center;
  font-family: "Inter", sans-serif;
}

.repairHeading {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: x-large;
  padding-bottom: 3vh;
}
.repairContent {
  font-size: small;
  font-weight: 300;
}

.repairContent-2 {
  margin-top: 20vh;
  margin-bottom: 20vh;
  display: grid;
  grid-template-rows: auto auto;
  justify-content: center;
  gap: 5rem;
}

.repair-img {
  height: 20rem;
  width: 18rem;
  object-fit: cover;
}

.repair-text {
  align-self: center;
}

.btn {
  margin-top: 4vh;
}
.btn a {
  text-decoration: none;
  color: #f7f7f7;
}

.repair-text h6 {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.repair-text h2 {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: xxx-large;
}
.repair-text h6 {
  margin-top: 5vh;
  font-weight: 600;
  color: rgb(236, 122, 122);
}
.repair-text p {
  font-weight: 400;
  color: black;
}

.customerReview {
  margin-top: 15rem;
  display: grid;
  justify-content: center;
  text-align: center;
}

.customerReview h1 {
  font: 400;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.customerReview p {
  color: rgb(0, 0, 0);
}

.our-loc {
  margin-top: 8vh;
  margin-bottom: 8vh;
  border: none;
  border-radius: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.repairCard-1,
.repairCard-2 {
  margin-top: 3rem;
  display: grid;
  justify-content: center;
  gap: 2rem;
  text-align: center;
}
.repairCard-1 .card {
  width: 15rem !important;
}
.repairCard-2 .card {
  width: 15rem !important;
}
.repairCard-2 {
  margin-top: 8rem;
  margin-bottom: -5rem;
}
.review-container h6 {
  margin-bottom: 2rem;
}

.carousel {
  margin-top: 2rem;
}

/* -----for mobile view ----- */

@media screen and (min-width: 1024px) {
  .repairHeading {
    font-size: xxx-large;
  }
  .repairContent {
    font-size: large;
  }
  .repairCard-1,
  .repairCard-2 {
    margin-top: 8rem;
    display: grid;
    grid-template-columns: 15rem 15rem 15rem;
    justify-content: space-evenly;
    align-items: center;
    gap: 5rem;
    text-align: center;
  }

  .repairContent-2 {
    display: grid;
    grid-template-columns: 1.2fr 1fr !important;
    grid-template-rows: 2fr;
    justify-content: center;
    gap: 10rem;
  }
  .repair-text p {
    font-weight: 600;
  }
  .customerReview h1 {
    font: 500;
  }
  .our-loc {
    margin-top: 8vh;
    margin-bottom: 8vh;
    border: none;
    border-radius: 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
  .repair-img {
    height: 30rem;
    width: 25rem;
    object-fit: cover;
  }
}
