.carousel {
  /* margin: 2rem; */
  text-align: center;
}

.carousel-indicators {
  display: none;
}
.carousel-control-prev,
.carousel-control-next {
  display: none;
}
.carouselImg {
  object-fit: cover;
  height: 300px !important;
  width: 350px;
}
@media screen and (min-width: 1024px) {
  .carousel {
    margin: 0rem;
    text-align: center;
  }
  .carouselImg {
    object-fit: cover;
    height: max-content !important;
    width: 100%;
  }
}
