.card-img {
  width: none;
}

.carousel-item .card-img {
  align-self: center;
  width: 18rem !important;
}

.card {
  position: none;
  width: none !important;
}
@media screen and (max-width: 1024px) {
  .card {
    position: none;
    width: 20rem;
  }
  .carousel-item .card-img {
    width: 9rem;
  }
}
