.header {
  text-transform: capitalize;
  font-weight: 500;
  background-image: linear-gradient(to right, #dee4ea, #f9fcff);
  padding: 0.3rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  top: 0;
  z-index: 10;
  font-size: x-large;
  font-family: "Inter", sans-serif;
  position: sticky;
}

.navbar-collapse {
  display: flex;
  justify-content: center;
  background-image: linear-gradient(to right, #dee4ea, #f9fcff);
  opacity: 100px;
}

.navbar-nav {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

.navbar-toggler {
  border: none;
  border-color: none;
  color: none;
}

button:focus {
  outline: none;
}
