* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* font-family: "Inter", sans-serif; */
}

.mainBody {
  background-color: #935d8c;
  background-image: url("./assert/img/overlay.png"),
    -moz-linear-gradient(45deg, #e37682 50%, #5f4d93 100%);
  background-image: url("src/img/overlay.png"),
    -webkit-linear-gradient(45deg, #e37682 50%, #5f4d93 100%);
  background-image: url("././assert/img/overlay.png"),
    -ms-linear-gradient(45deg, #e37682 50%, #5f4d93 100%);
  background-image: url("./assert/img/overlay.png"),
    linear-gradient(45deg, #e37682 15%, #5f4d93 100%);
  background-image: url("./assert/img/overlay.png")
    linear-gradient(45deg, #e37682 15%, #5f4d93 100%);
}

.subMainBody {
  margin: 1.2rem;
  margin-bottom: 0;
  /* background-color: #ffffff; */
  background-image: linear-gradient(#fbfbfb, #f6f6f6);
  border-radius: 2rem;
  padding: 0rem;
  text-transform: capitalize;
  /* position: relative; */
}
.mainContent {
  padding: 2rem;
}

@media screen and (min-width: 1024px) {
  .subMainBody {
    margin: 5rem;
  }

  ::-webkit-scrollbar {
    width: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: white;
    border-radius: 50rem;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(207, 197, 241);
  }
  ::-webkit-scrollbar-track {
    background: linear-gradient(45deg, #7f73ad 50%, #5f4d93 100%);
  }
}
