.footer {
  display: grid;
  grid-template-columns: 20rem;
  grid-auto-flow: row;
  padding: 2rem;
  gap: 4rem;
  color: white;
}

.footerContent {
  display: grid;
  flex-direction: column;
}
.footerButton {
  background-color: transparent;
  padding-right: 2rem;
  padding-left: 2rem;
}

.detail {
  display: grid;
  grid-template-columns: 150px 1fr;
}

.contactDetail {
  margin-bottom: 15px;
}
.contactDetailBlock p {
  font-size: small;
  align-self: center;
  margin-bottom: 0;
  gap: 1rem;
  font-family: "Inter", sans-serif;
}

.icons {
  margin-top: 10px;
  display: flex;
  padding: 1rem;
  gap: 3rem;
  justify-content: start;
}

.contactDetailBlock a {
  color: white;
  text-decoration: none;
}

.btn-16 {
  border: none;
  color: #000;
  justify-self: start;
}
.btn-16 p {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  text-align: center;
  font-size: 18px;
  color: #676767;
}
.btn-16:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  direction: rtl;
  z-index: -1;
  box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001;
  transition: all 0.3s ease;
}
.btn-16:hover {
  color: #000;
}
.btn-16:hover:after {
  left: auto;
  right: 0;
  width: 100%;
}
.btn-16:active {
  top: 2px;
}

.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}

@media screen and (min-width: 1024px) {
  .footer {
    grid-template-columns: auto auto;
    gap: 10rem;
    padding: 5rem;
  }
  .footerContent h2 {
    font-size: x-large;
  }
  .contactDetailBlock p {
    font-size: large;
    align-self: center;
    margin-bottom: 0;
    gap: 1rem;
    font-family: "Inter", sans-serif;
  }
  .icons {
    gap: 5rem;
  }
}
