.contact {
  display: flex;
  justify-content: center;
  text-align: center;
}

.location h2 {
  font-size: large;
  font-weight: 700;
}
.location h6 {
  font-size: small;
  font-weight: 100;
}

@media screen and (min-width: 1024px) {
  .location {
    margin-top: 5rem;
  }
  .our-loc {
    width: 1000px;
    height: 350px;
  }
  .location h2 {
    font-size: xx-large;
    font-weight: none;
  }
  .location h6 {
    font-size: none;
    font-weight: none;
  }
}
